<template>
	<view class="main">
		<tou_hand1 handtitle='绑定sim卡系统账号'></tou_hand1>
		<view class="tap">
			<view class="tapone">
				<view class="taponetxt">sim卡系统账号：</view>
				<input placeholder="sim卡系统账号" v-model="simAccount" />
			</view>
			<view class="tapone">
				<view class="taponetxt">密码：</view>
				<input placeholder="密码" v-model="password" />
			</view>
			<view class="taptwo" @click="sbtn">确定</view>
		</view>
	</view>
</template>

<script>
	import tou_hand1 from '../../components/tou_hand1/tou_hand1.vue'
	export default {
		components: {tou_hand1},
		mounted() {
			document.querySelector('body').setAttribute('style', 'background-color:#fff')
		},

		data() {
			return {
				simAccount:'',//sim卡系统账号
				password:'',//密码
			}
		},
		onLoad(option) {
			
		},
		methods: {
			sbtn(){
				let data={
					simAccount:this.simAccount,
					password:this.password
				}
				uni.showLoading({
					title: '请稍候',
					mask: true
				})
				this.$base.request1('manager/simBindUser/add', 'POST',data)
					.then(res => {
						uni.hideLoading()
						if(res.data.code==200){
							this.$toast.success('添加成功')
							uni.navigateBack()
						}else{
							this.$toast.fail(res.data.msg)
						}
					})
					.catch(err => {
				
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.main {
		width: 100%;
		overflow-x: hidden;
		.tap{
			width: 100%;
			padding: 0 32upx;
			box-sizing: border-box;
			.tapone{
				width: 100%;
				display: flex;
				align-items: center;
				margin-top: 30upx;
				.taponetxt{
					font-size: 28upx;
					font-weight: 600;
					width: 210upx;
					text-align: right;
				}
				input{
					width: 360upx;
					border: 2upx solid #333;
					padding: 10upx;
					border-radius: 10upx;
					margin-right: 20upx;
				}
				/deep/.uni-input-placeholder{
					font-size: 26upx;
					font-weight: 600;
				}
				/deep/.uni-input-input{
					font-size: 26upx;
					font-weight: 600;
				}
			}
			.taptwo{
				padding: 20upx;
				text-align: center;
				background-color: #4B98ED;
				color: #FFFFFF;
				font-size: 30upx;
				font-weight: 600;
				margin: 0 auto;
				margin-top: 190upx;
				border-radius: 10upx;
			}
		}
	}
</style>
